import React from 'react';

const PauseCircle = (props) => {
    let left = props.left ? props.left : 0;
    let top = props.top ? props.top : 0;
    let size = props.size ? props.size : "1em";
    let color = props.color ? props.color : 'currentColor';
    return (
        <svg xmlns="http://www.w3.org/2000/svg" width={size} height={size} strokeWidth="3" fill={color} style={{transform:"translate("+left+"px, "+top+"px)"}} className="bi bi-pause-circle" viewBox="0 0 16 16">
          <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
          <path d="M5 6.25a1.25 1.25 0 1 1 2.5 0v3.5a1.25 1.25 0 1 1-2.5 0v-3.5zm3.5 0a1.25 1.25 0 1 1 2.5 0v3.5a1.25 1.25 0 1 1-2.5 0v-3.5z"/>
        </svg>
        

    )
};

export default PauseCircle;
